<template>
  <div class="container">
		<img class="coinness" src="../../../static/img/coinness_logo_white--2.png" alt="" />
		<p class="slogan">빠르고 정확한 24시간 라이브 투자 정보</p>
		<button class="download" @click="downloadapp()">
			<img class="icon" src="../../../static/img/icondownload.png" alt="" />
			<span>APP다운로드</span>
		</button>
	</div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods:{
  	downloadapp(){
  		window.open("http://m.bishijie.com","_self");
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

body,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
div,
p,
ol,
table,
form,
section,
dl,
dt,
dd,
header,
footer {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
ul,
ol {
  list-style: none;
}
img {
  border: none;
  vertical-align: middle;
}
input,
select,
textarea {
  outline: none;
  border: none;
  background: none;
}
textarea {
  resize: none;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4 {
  font-weight: normal;
}
em,
i {
  font-style: normal;
}
.container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: url("../../../static/img/bg@2.png") no-repeat center center;
  background-size: cover;
  background-color: gray;
}
.container .coinness {
  display: block;
  margin: 0 auto;
  width: 3rem;
  height: 1.5rem;
  margin-top: 1.01rem;
}
.container .slogan {
  margin-top: 0.16rem;
  font-size: 0.18rem;
  color: #fff;
  text-align: center;
}
.container .download {
  cursor: pointer;
  display: block;
  width: 3rem;
  height: 0.4rem;
  margin: 0 auto;
  margin-top: 2.6rem;
}
.container .download .icon {
  width: 0.2rem;
  height: 0.2rem;
  margin-right: 0.1rem;
  vertical-align: middle;
}
.container .download span {
  display: inline-block;
  vertical-align: middle;
  margin-top: 1px;
}

</style>



