<template>
<transition name="detail">
	<section class="container">
		<h2 class="title">{{this.$store.state.newsdetail.title}}</h2>
		<div class="time">
			<span>星期</span>
			<span>2018.3.2</span>
			<span>13:30:59</span>
		</div>
		<div class="content">{{this.$store.state.newsdetail.content}}</div>
		<div class="share">
			<span @click="tosource(this.$store.state.newsdetail.link)">펼쳐보기</span><img @click="$store.commit('showshare')" src="../../../static/img/share@2.png" alt="" />
		</div>
		<button @click="backtolist()" class="btn-return">코인니스 최신 뉴스피드</button>
	</section>	
</transition>
</template>

<script>
import axios from 'axios';
export default {
  name: 'detail',
  data () {
    return {
      showsharebox: false,
      news:{}
    }
  },
  methods:{
  	tosource(url){
  		window.open(url,"_self");
  	},
  	backtolist(){
  		this.$router.push({ path:'/'});
  	}
  },
  mounted(){
	axios.get('http://www.bishijie.com/api/newsv17/detail?id='+this.$route.params.newsflash_id).then(function(res){
		return res
	}).then((res)=>{
		this.news=res
	});
  }
}	
</script>

<style scoped lang="less">
@import url("../../../static/less/z.less");
body,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
div,
p,
ol,
table,
form,
section,
dl,
dt,
dd,
header,
footer {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
ul,
ol {
  list-style: none;
}
img {
  border: none;
  vertical-align: middle;
}
input,
select,
textarea {
  outline: none;
  border: none;
  background: none;
}
textarea {
  resize: none;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4 {
  font-weight: normal;
}
em,
i {
  font-style: normal;
}
/*.detail-enter{transform:translateX(414px)}
.detail-enter-active{transition: all 1s}
.detail-leave-to{transform:translateX(414px)}
.detail-leave-active{transition: all 1s}*/
.container {
  position: absolute;
  top: 0.69rem;
  left: 0;
  right: 0;
  bottom: 0;
  padding-left: 0.2rem;
  padding-right: 0.2rem;
}
.container .title {
  font-size: 0.19rem;
  font-weight: bold;
  color: #333;
  .lh(23);
}
.container .time {
  margin-top: 0.13rem;
  font-size: 0.14rem;
  color: #999;
}
.container .content {
  margin-top: 0.15rem;
  font-size: 0.16rem;
  color: #151515;
  .lh(22);
  text-align: justify;
}
.container .content p {
  margin-top: 0.22rem;
}
.container .share {
  position: relative;
  margin-top: 0.48rem;
  font-size: 0.16rem;
  line-height: 0.16rem;
  margin-bottom: 0.4rem;
}
.container .share span {
  color: #0071ed;
  vertical-align: middle;
}
.container .share img {
  width: 0.16rem;
  height: 0.16rem;
  vertical-align: middle;
  position: absolute;
  right: 0;
}
.container .btn-return {
  border-radius: 4px;
  background-color: #fff;
  font-size: 0.15rem;
  color: #333;
  display: block;
  width: 3.02rem;
  height: 0.42rem;
  border: 1px solid #B0B0B1;
  margin: 0 auto;
  margin-bottom: 30px;
}
</style>


