// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';
import Vuex from 'vuex';
Vue.use(Vuex);
Vue.config.productionTip = false;
import axios from 'axios';

var store = new Vuex.Store({
	state:{
		shareon:false,
		data:{},
		index:1,
		newsdetail:{
			title:'',
			time:'',
			content:'',
			sourceurl:''
		},
		axioson:false,
		scrollTopLeaveIndex:0
	},
	mutations:{
		setScrollTopLeaveIndex(state,top){
			state.scrollTopLeaveIndex = top
		},
		showshare:function(state){
			state.shareon=true;
		},
		hideshare:function(state){
			state.shareon=false;
		},
		setdata(state,data){
			state.data=data;
		},
		pushmore(state,res){
			console.log(state.data,res)
			state.data.push(res);
			state.axioson=false;
		},
		addindex(state){
			state.index++
		},
		setnewsdetail(state,obj){
			state.newsdetail.title=obj.title;
			state.newsdetail.time=obj.time;
			state.newsdetail.content=obj.content;
			state.newsdetail.sourceurl=obj.link;
		}
	},
	actions:{
		getmore(context){
			let index=context.state.index;
			context.commit('addindex');
			if(context.state.axioson===false){
				context.state.axioson=true;
				axios.get('http://www.bishijie.com/api/newsv17/index?size=50').then(function(res){
					alert(123)
					return res
				}).then((res)=>{
					context.commit('pushmore',res.data.data);
				});	
			}
		}
	}
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
	beforeMount(){
//		axios.get('http://www.bishijie.com/api/newsv17/index?size=50').then(function(res){
//			return res
//		}).then((res)=>{
//			this.$store.commit('setdata',res.data.data)
//			console.log(res.data.data)
//		});
	},
	data:{
		
	},
	methods:{
		
	}
})



