<template>
  <div id="app">
  	<cheader></cheader>
  	<cheaderback></cheaderback>
    <router-view/>
    <cshareto></cshareto>
  	<!--<cfooter></cfooter>-->
  </div>
</template>

<script>
import cfooter from './components/common/cfooter'
import cheader from './components/common/cheader'
import cheaderback from './components/common/cheaderback'
import cshareto from './components/common/cshareto'


export default {
  name: 'App',
  data(){
  	return{
  		
  	}
  },
  methods:{
  	
  },
  components:{
  	cfooter,
  	cheader,
  	cheaderback,
  	cshareto
  }
}



var oHtml = document.documentElement;
    	getSize();
	    window.onresize = function(){
	        getSize();
	    };
	    function getSize(){
	        var screenWidth = oHtml.clientWidth;
	        if (screenWidth < 320) {
	            oHtml.style.fontSize = '42.6667px';
	}
	        else if(screenWidth > 375){
	    oHtml.style.fontSize = '100px';
	}
	        else{
	    oHtml.style.fontSize = screenWidth/(375/100) + 'px';
	}
}
</script>

<style scoped>


</style>



