<template>
<transition name="index">
  <div ref="box" class="container">
  	<div class="day" v-for="value in this.$store.state.data">
			<div class="fixtoptime">
				<img src="../../../static/img/time2x.png" alt="" />
				<span>{{value.top}}</span>
			</div>
			<ul class="newscontainer">
				<li v-for="v in value.buttom"  @click.stop="gotodetail(v.newsflash_id);$store.commit('setnewsdetail',{title:v.title,time:v.issue_time,content:v.content,link:v.link});setscrolltopleave()">
					<div class="content">
						<h3><span class="newstime">{{new Date(v.issue_time).getHours()+":"+new Date(v.issue_time).getSeconds()}}</span>{{v.title}}</h3>
						<p>{{v.content}}</p>
						<div class="sourceshare">
							<i class="source" :data-url="'url'">查看原文</i>
							<img @click.stop="$store.commit('showshare')" src="../../../static/img/share@2.png" alt="" class="share" />
						</div>
					</div>
				</li>
			</ul>  		
  	</div>
	</div>	
</transition>
</template>

<script>
import axios from 'axios';
	
export default {
  name: 'index',
  data () {
    return {
    	width:0,
			scrollTop:0,
    	clientHeight:0,
    	scrollHeight:0
    }
  },
  beforeMount(){
  	window.addEventListener('scroll',this.scroll,false);
  	axios.get('http://www.bishijie.com/api/newsv17/index?size=50').then(function(res){
			return res
		}).then((res)=>{
			this.$store.commit('setdata',res.data.data)
			console.log(res.data.data)
		});
		this.width=document.documentElement.clientWidth
  },
  mounted(){
  	console.log(document.documentElement.scrollTop=this.$store.state.scrollTopLeaveIndex)
  },
  methods:{
		gotodetail(newsflash_id){
			this.$router.push({ path:'/newsflash/detail/'+newsflash_id});
		},
		setscrolltopleave(){
			this.$store.commit('setScrollTopLeaveIndex',this.scrollTop);
			console.log(this.$store.state.scrollTopLeaveIndex)
		},
		getScrollTop(){
		    this.scrollTop = document.documentElement.scrollTop;
		},
		getClientHeight() {
	        this.clientHeight = document.documentElement.clientHeight
		},
		getScrollHeight() {
			this.scrollHeight = document.documentElement.scrollHeight
		},
		scroll(){
			this.getScrollTop();
			this.getClientHeight();
			this.getScrollHeight();
			if(this.scrollTop + this.clientHeight + 50 >= this.scrollHeight) {
				this.$store.dispatch('getmore')
			}
		}
	},
	destroyed(){
		window.removeEventListener('scroll',this.scroll,false);
	},
  components:{
  	
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import url("../../../static/less/z.less");
/*.index-enter{transform:translateX(-414px)}
.index-enter-active{transition: all 1s}
.index-leave-to{transform:translateX(-414px)}
.index-leave-active{transition: all 1s}*/
.container{
	.pt(40);
	.fixtoptime{
		z-index:2;
		position:fixed;
		.t(40);
		left:0;
		width: 100%;
		.h(30);
		.fs(13);
		.lh(30);
		background-color: #F2F8FF;
		.pl(15);
		img{
			.h(18);
			.w(18);
			.mt(-2);
		}
	};
	.newscontainer{
		.pt(30);
		.pl(45);
		.pr(15);
		li{
			
		}
		.content{
			h3{
				text-align: justify;
				color: #333;
				position: relative;
				.mt(16);
				.fs(16);
				.lh(22);
				font-weight: bold;
				span{
					position: absolute;
					.l(-38);
        	-webkit-transform-origin-x: 0;
        	-webkit-transform: scale(0.80);
					.fs(10);
					.lh(12);
					.mt(3);
					.pt(1);
					.pb(1);
					.pl(1);
					.pr(1);
					background-color: #0071ED;
					color: #fff;
					border-radius: 10px;
					.w(43);
					display: inline-block;
					text-align: center;
				}
			}
			p{
				text-align: justify;
				color: #151515;
				.fs(15);
				.mt(9);
				.lh(21);
				.h(63);
				overflow: hidden;
			}
			.sourceshare{
				position: relative;
				.mt(15);
				.fs(15);
				.source{
					.fs(15);
					color: #0071ED;
					font-weight: bold;
				}
				img{
					position: absolute;
					right: 0;
					.w(15);
					.h(15);
				}
			}
		}
		
	}
}
</style>



