import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/newsflash/index'
import download from '@/components/download/download'
import detail from '@/components/newsflash/detail'
import cfooter from '@/components/common/cfooter'
import cheader from '@/components/common/cheader'
import cheaderback from '@/components/common/cheaderback'
import cshareto from '@/components/common/cshareto'
import Mint from 'mint-ui';

Vue.use(Router);
Vue.use(Mint);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/download',
      name: 'download',
      component: download
    },
    {
      path: '/common/cfooter',
      name: 'cfooter',
      component: cfooter
    },
    {
      path: '/common/cheader',
      name: 'cheader',
      component: cheader
    },
    {
      path: '/common/cheaderback',
      name: 'cheaderback',
      component: cheaderback
    },
    {
      path: '/common/cshareto',
      name: 'cshareto',
      component: cshareto
    }
    ,{
      path: '/newsflash/detail/:newsflash_id',
      name: 'detail',
      component: detail
    }
  ]
})



