<template>
	<div class="cheaderback" v-if="$route.name!='index'">
		<img @click="backto()" src="../../../static/img/arrow.png" alt="" class="iconback" />
	</div>
</template>

<script>
export default {
  name: 'cheaderback',
  data () {
    return {
      msg: ''
    }
  },
  methods:{
  	backto(){
  		this.$router.push({path:'/'})
  	}
  }
}	
</script>

<style scoped>
.cheaderback{
	z-index: 2;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 0.4rem;
	text-align: center;
	background-color: #0874e9;
	line-height: 0.4rem;
	color: #fff;
	font-weight: bolder;
}
.cheaderback img{
	position: absolute;
	left: 0.2rem;
	height: 0.2rem;
	width: 0.13rem;
	top:0.2rem;
	margin:-0.1rem
	
}		
</style>


