<template>
	<ul class="cfooter clearfix" v-if="$route.path!='/newsflash/detail'">
		<li @click="tonewsflash()"><img :src=newsimgon alt="" /><p>news</p></li>
		<li @click="tomypage()"><img :src=pageimgon alt="" /><p>My page</p></li>
	</ul>
</template>

<script>
export default {
  name: 'cfooter',
  data () {
    return {
      pagenow:"",
      newslist:"",
      mypage:""
    }
  },
  computed:{
  	newsimgon(){
  		let url="";
  		if(this.$route.name=='index'){
  			url="../../../static/img/kuaixun.png"
  		}else{
  			url="../../../static/img/kuaixun2.png"
  		};
  		return  url;
  	},
  	pageimgon(){
  		let url="";
  		if(this.$route.name=='mypage'){
  			url="../../../static/img/mypage.png"
  		}else{
  			url="../../../static/img/mypage2.png"
  		};
  		return  url;
  	}
  },
  methods:{
  	tonewsflash(){
  		console.log(this.$route);
  		this.$router.push({path : '/'})
  	},
  	tomypage(){
//		this.$router.push({path : '/mypage'})
  	}
  }
}	
</script>

<style scoped lang="less">
@import url("../../../static/less/z.less");
.cfooter{
	z-index:2;
	position:fixed;
	left:0;
	bottom:0;
	background-color:#fff;
    box-shadow: 0 1px 8px #eee;
	width:100%;
	.h(54);
	li{
		position:relative;
		top:50%;
		.mt(-16);
		width:50%;
		text-align:center;
		img{
			.w(22);
			.h(22);
		};
		.fs(10);
		.fl;
	}
}
</style>


