<template>
	<div class="cshareto" v-if="this.$store.state.shareon==true">
		<div class="container">
			<p class="title">shareto</p>
			<ul class="clearfix">
				<li @click="tofa">
					<img src="../../../static/img/facebook@2x.png" alt="" />
					<p>facebook</p>
				</li>
				<li @click="totw">
					<img src="../../../static/img/twitter3@2x.png" alt="" />
					<p>twitter</p>
				</li>
				<li @click="toli">
					<img src="../../../static/img/line@2x.png" alt="" />
					<p>line</p>
				</li>
				<li @click="tote">
					<img src="../../../static/img/telegram@2x.png" alt="" />
					<p>telegram</p>
				</li>
				<li @click="toka">
					<img src="../../../static/img/kakao@2x.png" alt="" />
					<p>kakao</p>
				</li>
			</ul>
			<span class="line"></span>
			<p class="cancel" @click="$store.commit('hideshare')">cancel</p>
		</div>
	</div>
</template>

<script src="../../../static/js/loader.js"></script>
<script src="../../../static/js/analytics.js"></script>
<script src="../../../static/js/kakao.js"></script>
<script>
export default {
  name: 'cshareto',
  data () {
    return {
      url:encodeURIComponent(document.location.href)
    }
  },
  props:{
	
  },
  methods:{
  	tofa(){
  		window.open('http://www.facebook.com/sharer.php?u='+encodeURIComponent(document.location.href)+'&amp;t='+encodeURIComponent(document.title),'_blank','toolbar=yes, location=yes, directories=no, status=no, menubar=yes, scrollbars=yes, resizable=no, copyhistory=yes, width=600, height=450,top=100,left=350');
  	},
  	totw(){
  		window.open('http://twitter.com/home?status='+encodeURIComponent(document.location.href)+' '+encodeURIComponent(document.title),'_blank','toolbar=yes, location=yes, directories=no, status=no, menubar=yes, scrollbars=yes, resizable=no, copyhistory=yes, width=600, height=450,top=100,left=350');
  	},
  	toli(){
  		window.open('https://social-plugins.line.me/lineit/share?url='+encodeURIComponent(document.location.href))
  	},
  	tote(){
  		window.open("https://telegram.me/share/url?url="+encodeURIComponent(document.location.href)+"&text="+encodeURIComponent(document.title));
  	},
  	toka(){
		window.open("https://story.kakao.com/s/share?url="+encodeURIComponent(document.location.href)+"&text="+document.title)
  	},
  }
}	
</script>

<style scoped lang="less">
@import url("../../../static/less/z.less");
.cshareto{
	z-index:3;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgba(0,0,0,.7);
	.container{
		/*position:relative;*/
		.h(240);
		.w(345);
		position:absolute;
		.b(170);
		background-color:#eee;
		left:50%;
		.ml(-172);
		border-radius:5px;
		.title{
			.fs(16);
			color:#333;
			.pt(15);
			text-align:center;
			    font-weight:bold ;
		}
		ul{
			.fs(10);
			.mt(12);
			li{
				.fl;
				.w(115);
				text-align:center;
				img{
					.w(24);
					.h(25);
				};
				p{
					.mt(6);
					.fs(10);
					color:#666;
				}
			}
			li:nth-of-type(4),li:nth-of-type(5){
				.mt(25);
			}
		}
		.line{
			color:#666;
			position:absolute;
			width:100%;
			display:block;
			border-top:1px solid #eee;
			.b(49);
				
		}
		.cancel{
			.fs(18);
			color:#FF1C1C;
			position:absolute;
			.b(14);
			left:50%;
			.ml(-30);
		}
	}
}
</style>


