<template>
	<div class="cheader" v-if="$route.name=='index'">
		<h3>{{msg}}</h3>
	</div>
</template>

<script>
export default {
  name: 'cheader',
  data () {
    return {
      msg: '韩国字儿'
    }
  },
  methods:{
  	
  }
}	
</script>

<style scoped>
.cheader{
	z-index: 2;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 0.4rem;
	text-align: center;
	background-color: #0874e9;
	line-height: 0.4rem;
	color: #fff;
	font-weight: bolder;
}
.cheader h3{
	font-size: 0.18rem;
}	
</style>


